<template>
  <footer class="footer">
    <ul>
      <li v-for="(item,index) in this.bottomNavData" :key="index" :class="hasActive(item)">
        <router-link :to="{path:item.path}">
          <i class="fa" :class="item.iconName"></i>
          <p v-text="item.text"></p>
        </router-link>
      </li>
    </ul>
  </footer>
</template>

<script>
export default {
  data() {
    return{
      bottomNavData: [],
      changeId: 0,
    }
  },
  watch: {
    changeId: {
      immediate: true,
      handler() {
        this.$root.eventHub.$on('changeIdBus',(index) =>{
          this.changeId = index;
          this.refreshData(this.changeId);
        })
      }
    }
  },
  mounted(){
    this.refreshData(this.changeId);
  },
  destroyed(){
    this.$root.eventHub.$off('changeIdBus')
  },
  methods:{
    refreshData(id){
      let bNavData = [
        {path: '/?id='+id+'',iconName:'fa-file-o',text: '资讯'},
        {path: '/activity',iconName:'fa-search-plus',text: '活动'},
        {path: '/employment',iconName:'fa-wpexplorer',text: '招聘'},
        {path: '/university',iconName:'fa-university',text: '大学'},
        {path: '/personal',iconName:'fa-user-o',text: '个人'},
      ]
      this.bottomNavData = bNavData
    },
    hasActive(item){
      if(this.$route.fullPath === item.path){
        return {
          active: true
        }
      }else{
        return {
          active: false
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>

.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1000;
  ul{
    height: px2rem(95);
    display: flex; 
    align-items: center;
  }
  li{
    flex: 1;
    text-align: center;
    &.active{
      a{
        color:#f26d21;
      }
    }
    a{
      display: block;
      color: #6c6f71;
    }
    i{
      font-size: px2rem(40);
    }
    p{
      margin-top: px2rem(10);
      font-size: px2rem(24);
    }
  }
}
</style>




